.mapbox {
    position: relative;
    height: 250px;
    width: 100%;
    border-radius: 16px;
    margin-bottom: 30px;
    border: 1px solid var(--jet);
    overflow: hidden;
}

.mapbox figure {
    height: 100%;
}

.mapbox iframe {
    width: 100%;
    height: 100%;
    border: none;
    /* filter: grayscale(1) invert(1); */
}

.map-container {
    width: 400px;
    height: 300px;
}


@media (min-width: 580px) {
    /* MAP */
    .mapbox {
        height: 380px;
        border-radius: 18px;
    }
}